import React from 'react';

function Modal({currentPhoto, onClose}) {
    const {name, category, description, index} = currentPhoto;

    return(
        <div id="backdrop" className="modalBackdrop" onClick={onClose}>
            <div className="modalContainer">
                <h3 className="modalTitle">{name}</h3>
                <img src={require(`../../assets/large/${category}/${index}.jpg`).default} alt={name} />
                <p>
                    {description}
                </p>
                <button id="close-button" type="button" data-testid="close-modal" onClick={onClose}>Close this modal</button>
            </div>
        </div>
    );
}

export default Modal;